<style lang="stylus" scoped>
    .index 
        height 100%
        position relative
    .aside 
        height 100%
        background #fff
        display flex
        width 22%
        flex-wrap wrap
        img
            display block
            max-width 100%
        .item 
            min-height 16.6%
            min-width 33.3%
    .main 
        position absolute
        top 0
        left 22%
        right 0 
        bottom 0  
        background #fff     
        padding-left 20px
        padding-right 20px
        overflow auto
        .log 
            margin 15px 0 0 0
            background #e3e1e6
            padding 10px 
            border-radius 10px
            &:first-child 
                margin-top 5px
            .title 
                font-size 24px
                color #000 
                font-weight bold 
                padding-bottom 15px
                border-bottom 1px solid #eee
            .log-con 
                //padding-top 5px
</style>

<template lang="pug">
    .index
        .aside 
            img(:src="imgUrl")
            //- div.item(v-for="(item,index) in list" :style="{background:`url(${imgUrl}) ${index*33.3}px ${index*33.3}px`}") {{item}} {{index}}
        .main 
            el-tabs(v-model="activeName" @tab-click="tabSwitch")
                el-tab-pane(label="日志" name='log') 
                    .log 
                        .title 前端收集 
                        .log-con 
                            p 
                                |在前端路上摸索前行，在这里分享自己长期关注的前端开发相关的优秀网站、博客、以及活跃开发者。欢迎更新，以下各排名不分先后顺序。
                            p 
                                |自己 RSS 长期订阅了一些IT 和技术相关博客，这里是我Feedly 输出的opml，可直接导入一些RSS 阅读器:
                            p 
                                a(href="https://github.com/foru17/luolei-dotfiles/blob/master/feedly.opml" target="_blank") https://github.com/foru17/luolei-dotfiles/blob/master/feedly.opml
                    .log 
                        .title React Native 如何区分系统平台，动态加载组件 
                        .log-con 
                            p 
                                |今天分享的内容是使用Platfrom的一个小技巧。相信大家都使用过Platfrom.os来判断移动设备平台是ios还是android。那么Platform的select你是否知道呢？快来了解一下吧…
                            p 
                                a(href="http://blog.csdn.net/u013718120/article/details/78291945" target="_blank") http://blog.csdn.net/u013718120/article/details/78291945 
                    .log 
                        .title vue-cli生成的项目，运行 npm run dev 报错 
                        .log-con 
                            p 
                                |2018年1月9日下午vue更新了，导致vue-cli生成的项目，运行 npm run dev 报错
                            p 
                                |解决方案：cnpm i之后，cnpm i -D webpack-dev-server@2.9.7
                                                         
                el-tab-pane(label="小游戏" name='game')
                    .log 
                        .title 拼图小游戏 
                        .log-con 
                            p 
                                a(href="https://hero7212.github.io/MLprizz/index.html" target="_blank") https://hero7212.github.io/MLprizz/index.html

                el-tab-pane(label="我封装的组件" name='compos')
                    .log 
                        .title 仿优酷轮播组件（基于react） 
                        .log-con 
                            p 
                                a(href="https://hero7212.github.io/reactYuKuBanner/index.html" target="_blank") https://hero7212.github.io/reactYuKuBanner/index.html
</template>

<script>

export default {
    name: "index",
    data() {
        return {
            list: [],
            imgUrl: "http://7s1r1c.com1.z0.glb.clouddn.com/t_nw10.jpg",
            activeName: 'log'
        };
    },
    
    methods: {
       tabSwitch(tab, event) {
           console.log(tab, event);
       }
    },
  
    created() {
        for(let i=0; i<18; i++) {
            this.list.push(i)
        }

        let n = 10
        setInterval(()=>{
            n++
            if(n>28){
                n = 10
            }
            this.imgUrl = `http://7s1r1c.com1.z0.glb.clouddn.com/t_nw${n}.jpg`
        },3000)
        
    },
    
    mounted() {
        
    }
};
</script>



